}

.form-floating>.form-control-plaintext~label::after,
.form-floating>.form-control:focus~label::after,
.form-floating>.form-control:not(:placeholder-shown)~label::after,
.form-floating>.form-select~label::after {
  background-color: $white;
  border-radius: $border-radius;
}

.form-control.is-valid:focus,
.was-validated .form-control:valid:focus {
  border-color: $success;
  box-shadow: none;
}

.form-control.is-valid,
.was-validated .form-control:valid {
  border-color: $success;
}

.form-control-color {
  width: 1.75rem;
  height: 1.75rem;
  overflow: hidden;
  padding: 0;
}

.bootstrap-tagsinput {
  border-color: $input-border;
  color: $text-color;
  background-color: $form-control-bg;
  font-size: $font-size-15;
  font-weight: $font-weight-normal;
  border-radius: 5px;
  padding: 0.45rem 0.625rem;
  height: 38px;
  box-shadow: none;
  overflow-y: auto;
  width: 100%;

  .tag {
    background: $light;
    border-radius: 5px;
    padding: 4px 5px;
    font-size: $font-size-14;
    color: $gray-700;
    display: inline-flex;
    align-items: center;

    span {
      &::after {
        color: $gray-900;
      }
    }

    [data-role="remove"]:hover {
      box-shadow: none;

      &:active {
        box-shadow: none;
      }
    }
  }
}

[dir="rtl"] {

  [type="email"],
  [type="number"],
  [type="tel"],
  [type="url"] {
    direction: rtl;
  }
}

.form-select.is-invalid:not([multiple]):not([size]) {
  padding-right: 1.125rem;
  background-size: 16px 28px, calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

input[type="range"]::-webkit-slider-runnable-track {
  background-color: $light;
}

[dir="rtl"] {
  .form-checked-outline:checked[type="checkbox"]:before {
    inset-inline-start: 1px;
  }
}

.form-check {
  min-height: inherit !important;
}

.col-form-label-lg,
.col-form-label-sm,
.col-form-label {
  font-weight: $font-weight-semibold;
}

.col-form-label {
  font-size: $font-size-15;
  font-weight: $font-weight-medium;
  color: $title-color;
}

.form-label {
  font-size: $font-size-14;
  font-weight: $font-weight-medium;
  color: $title-color;
}

[data-theme-mode="dark"] {
  input {

    &[type="week"],
    &[type="month"],
    &[type="date"],
    &[type="datetime-local"],
    &[type="time"] {
      &::-webkit-calendar-picker-indicator {
        filter: invert(1);
      }
    }
  }
}

[dir="rtl"] {
  input {

    &[type="week"],
    &[type="month"],
    &[type="date"],
    &[type="datetime-local"],
    &[type="time"] {
      text-align: end;
    }
  }
}

.input-group-text {
  border-color: $light-900;
}

/**
Form help
 */
.form-help {
  display: inline-flex;
  font-weight: $font-weight-bold;
  align-items: center;
  justify-content: center;
  width: 1.125rem;
  height: 1.125rem;
  font-size: .75rem;
  color: $white;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
  background: $secondary;
  border-radius: 4px;

  &:hover,
  &[aria-describedby] {
    color: $white;
    background: $primary;
  }
}

.input-icon {
  position: relative;

  .form-control:not(:last-child),
  .form-select:not(:last-child) {
    padding-right: 2.5rem;
  }
}


/* Input Group */

.input-group-link {
  font-size: $font-size-12;
}

.input-group-flat {
  &:focus-within {
    border-radius: $border-radius;

    .form-control,
    .input-group-text {
      border-color: $border-color !important;
    }
  }

  .form-control {
    &:focus {
      box-shadow: none;
    }

    &:not(:last-child) {
      border-right: 0;
    }

    &:not(:first-child) {
      border-left: 0;
    }
  }

  .input-group-text {
    &:first-child {
      padding-right: 0;
    }

    &:last-child {
      padding-left: 0;
    }
  }
}

.custom-radio {
  display: block;
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  font-size: $font-size-15;
  color: $gray-700;
  text-transform: capitalize;

  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    &:checked {
      ~.checkmark {
        border-color: $primary;
        background: $primary;

        &::after {
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }

  .checkmark {
    width: 20px;
    height: 20px;
    border: 1px solid $light-900;
    border-radius: 50%;
    -webkit-transition: all 0.3s;
    -ms-transition: all 0.3s;
    transition: all 0.3s;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;

    &::after {
      content: "";
      width: 8px;
      height: 8px;
      background-color: $white;
      opacity: 0;
      visibility: hidden;
      border-radius: 50%;
      -webkit-transition: all 0.3s;
      -ms-transition: all 0.3s;
      transition: all 0.3s;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

    }
  }
}

.check-radio-group {
  flex-wrap: wrap;
  row-gap: 10px;

  .custom-radio {
    margin-right: 15px;
  }
}

.check {
  display: block;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.checktoggle {
  background-color: rgba(115, 103, 240, 0.2);
  border-radius: 12px;
  cursor: pointer;
  display: block;
  font-size: 0;
  height: 24px;
  margin-bottom: 0;
  position: relative;
  width: 46px;
}

.status-toggle.modal-status label {
  height: 16px;
  width: 30px;
  background-color: $white;
  border: 1px solid $light-900;
  margin-bottom: 0;
}

.check:checked+.checktoggle {
  background-color: $white;
}

.checktoggle:after {
  content: " ";
  display: block;
  transform: translate(2px, -50%);
  width: 10px;
  height: 10px;
  background-color: $gray-300;
  transition: left 300ms ease, transform 300ms ease;
  border-radius: 50%;
  transform: translate(2px, -50%);
  -webkit-transform: translate(4px, -50%);
  -ms-transform: translate(2px, -50%);
  position: absolute;
  top: 50%;
  left: 0;
}

.check:checked+.checktoggle:after {
  background-color: $primary;
  left: 100%;
  transform: translate(calc(-90% - 4px), -50%);
}

.input-icon {
  position: relative;
}

.input-icon-addon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 2.5rem;
  color: $gray-700;
  pointer-events: none;
  font-size: 1.2em;

  &:last-child {
    right: 0;
    left: auto;
  }
}

.input-icon-start {
  .form-control {
    padding-left: 28px;
  }
}

.icon-addon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 2rem;
  color: $gray-700;
  pointer-events: none;
  font-size: 1.2em;
}

.form-wrap {
  .form-wrap-icon {
    position: relative;

    i {
      font-size: $font-size-18;
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
    }
  }
}

.toggle-password {
  cursor: pointer;
  width: 30px;
  height: 30px;
  color: $gray-900;
  font-size: $font-size-18;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  justify-content: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  @include rounded(50%);
  @include transform(translateY(-50%));
  position: absolute;
  top: 50%;
  right: 10px;

  &:hover {
    color: $primary;
  }
}

.pass-group {
  position: relative;
}

.toggle-passwords,
.toggle-passworda {
  cursor: pointer;
  width: 30px;
  height: 30px;
  color: $gray-900;
  font-size: $font-size-18;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  justify-content: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  @include rounded(50%);
  @include transform(translateY(-50%));
  position: absolute;
  top: 50%;
  right: 10px;

  &:hover {
    color: $primary;
  }
}

.form-check {
  .form-check-label {
    font-size: $font-size-14;
    color: $gray-600;
  }
}

.form-switch {
  .form-check-input {
    width: 36px;
    height: 20px;
  }

  .form-check-label {
    font-size: $font-size-14;
    color: $gray-600;
  }
}

.form-radio,
.form-checkbox {
  border: 1px solid $border-color;
  @include rounded(5px);
  background-color: $white;
  padding: 8px;

  label {
    color: $gray-600;
  }

  &.active {
    background-color: $light-300;

    label {
      color: $gray-900;
    }
  }
}